export class View {
    constructor() {
        this.title = document.querySelector('.film__title');
        this.animeBackground = document.querySelector('.hero__background');
        this.description = document.querySelector('.film__description');
        this.rating = document.querySelector('.film__rating');
        this.episodes = document.querySelector('.film__episodes');
        this.genres = document.querySelector('.film__tags');
        this.gallery = document.querySelector('.gallery');
    }
    async renderTitle(callback) {
        const anime = await callback;
        try {
            this.title.textContent = anime.name;
            this.description.textContent = anime.description;
            this.rating.textContent = anime.score;
            this.episodes.textContent = `${anime.episodes} серий`;
            this.genres.textContent = anime.genres.join(' | ');
            this.animeBackground.src = anime.screenshots;
        }
        catch (error) {
            console.error(error);
        }
    }
    async renderRelatedTittles(callback) {
        const animeList = await callback;
        animeList.forEach((anime) => {
            this.render(anime);
        });
    }
    render(anime) {
        const card = document.createElement('button');
        card.id = `${anime.id}`;
        card.classList.add('card');
        const cardImage = document.createElement('img');
        cardImage.src = `https://shikimori.one${anime.image.preview}`;
        cardImage.classList.add('card__image');
        const cardTitle = document.createElement('span');
        cardTitle.textContent = anime.russian;
        cardTitle.classList.add('card__text');
        card.append(cardImage, cardTitle);
        this.gallery.append(card);
    }
}
